import { createNamespacedHelpers } from "vuex";
const { mapState } = createNamespacedHelpers("layout");

export default {
  name: "NavigationDrawer",
  components: {},
  props: [],
  data() {
    return { drawer: false };
  },
  mounted() {
    this.$root.$on("toggleDrawer", () => {
      this.toggleDrawer();
    });
  },
  computed: {
    ...mapState({
      items: state => state.items
    })
  },
  methods: {
    toggleDrawer() {
      this.drawer = !this.drawer;
    }
  }
};



